workbox_routing_registerRoute("/", new workbox_strategies_NetworkFirst({ "cacheName":"start-url", plugins: [{ cacheWillUpdate: async ({ request, response, event, state }) => { if (response && response.type === 'opaqueredirect') { return new Response(response.body, { status: 200, statusText: 'OK', headers: response.headers }) } return response } }, { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(({ url }) => {
        const authPaths = [
          '/api/auth',
          '/login',
          '/register',
          '/reset-password',
          '/sign-in',
          '/sign-out',
          '/__/auth',
        ];
        
        return authPaths.some(path => 
          url.pathname.startsWith(path) || 
          url.pathname.includes('/auth/')
        );
      }, new workbox_strategies_NetworkOnly({ "cacheName":"auth-cache", plugins: [{ handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(({ url }) => {
        return (
          url.hostname.includes('firebaseio.com') || 
          url.hostname.includes('googleapis.com') ||
          url.hostname.includes('firebase')
        );
      }, new workbox_strategies_NetworkFirst({ "cacheName":"firebase-api-cache","networkTimeoutSeconds":10, plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 100, maxAgeSeconds: 3600 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(({ url }) => url.pathname.startsWith('/api/'), new workbox_strategies_NetworkFirst({ "cacheName":"api-cache","networkTimeoutSeconds":10, plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 100, maxAgeSeconds: 300 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(/^https?:\/\/.*\.(png|jpg|jpeg|svg|gif|webp|ico)$/i, new workbox_strategies_CacheFirst({ "cacheName":"image-cache", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 1000, maxAgeSeconds: 2592000 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(/^https?:\/\/fonts\.(?:googleapis|gstatic)\.com\/.*/i, new workbox_strategies_CacheFirst({ "cacheName":"google-fonts-cache", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 4, maxAgeSeconds: 31536000 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(/.*/, new workbox_strategies_NetworkFirst({ "cacheName":"default-cache","networkTimeoutSeconds":10, plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 200, maxAgeSeconds: 86400 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');




